<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback" @click="$router.back()" />
      <!-- 搜索组件 -->
      <van-search placeholder="请输入搜索关键词" v-model.trim="kw" background="#007BFF" shape="round" v-selfFocus @input="iptFn"
        @search="searchFn(kw)" />
    </div>

    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="kw.length !== 0">
      <div class="sugg-item" v-for="(item, index) in suggestList" :key="index" v-html="lightFn(item, kw)"
        @click="searchFn(item)">
      </div>
    </div>

    <!-- 搜索历史 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearFn" />
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list">
        <span class="history-item" v-for="(item, index) in history" :key="index"
          @click="searchFn(item)">{{ item }}</span>
      </div>
    </div>
  </div>
</template>

<script>
// 导入搜索联想API
import { suggestListAPI } from '@/api/index'

export default {
  name: 'Search',
  data() {
    return {
      kw: '', // 搜索关键字
      timer: null, // 防抖变量
      suggestList: [],
      history: JSON.parse(localStorage.getItem('historySearch')) || []// 建议列表
    }
  },
  methods: {
    // 输入框内容改变触发的方法
    iptFn() {
      // 防抖-先清除上一次的timer
      clearTimeout(this.timer)

      if (this.kw.length === 0) {
        this.suggestList = []
      } else {
        // 防抖
        this.timer = setTimeout(async () => {
          const { data: res } = await suggestListAPI({ kw: this.kw })
          this.suggestList = res.data.options
        }, 300)
      }
    },
    // 处理字符串高亮关键字
    lightFn(originStr, target) {
      // originStr:原字符串
      // target:关键字

      // 正则表达式
      const reg = new RegExp(target, 'ig')
      return originStr?.replace(reg, (match) => {
        return `<span style="color:red;font-weight:bold">${match}</span>`
      })
    },
    // 搜索事件触发的方法
    searchFn(kw) {
      if (kw?.length > 0) {
        if (this.history?.indexOf(kw) === -1) {
          this.history.push(kw)
          // 然后把值存到本地(存储到本地要用字符串)，避免组件切换值还是原来的
          localStorage.setItem('historySearch', JSON.stringify(this.history))
        }
        this.$router.push(`/search_result/${kw}`)
      }
    },
    // 清空历史列表
    clearFn() {
      localStorage.removeItem('historySearch')
      this.history = []
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;

  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }

  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}

/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;

  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
